*{
  font-family: 'Montserrat', sans-serif;
}
  
.moreprofile-info-card {
    /* position: flex; */
    /* background-color: rgb(78, 80, 78); */
    position: absolute;
    display: flex;
    flex-direction: row;
    background-color: rgb(0, 0, 0);
    width: 850px;
    height: 500px;
    border-radius: 10px;
    background-size: cover;
    background-position: center;
    /* cursor: pointer; */
    box-shadow: -80px -15px 25px rgba(0, 0, 0, 0.199);
    transition: all .15s;
    // margin-left: -390px;
    // margin-top: 150px;
    z-index: 1000;
    animation: fadeInAnimation ease-in-out .3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;

  }


.moreprofile-info-img {
    display: flex;
    flex-direction: row;
    justify-content: left;
    height: 500px;
    width: 460px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }


.moreprofile-location-icon-here {
   filter: invert(100%);
   height: 70%;
  }

.moreprofile-info-box {
  display: flex;
  justify-content: top, center;
  text-align: center;
  flex-direction: column;
   padding-left: 5%;
  padding-right: 5%; 
}

.moreprofile-card-title {
  color: white;
  display: flex;
  justify-content: left;
  font-size: 400%;
  padding-bottom: 0%;
  margin-top: 5%;
  margin-bottom: 1%;
  font-family: 'Montserrat', sans-serif;
}

.moreprofile-card-title-long {
  color: white;
  display: flex;
  justify-content: left;
  font-size: 300%;
  padding-bottom: 0%;
  margin-top: 5%;
  margin-bottom: 1%;
  font-family: 'Montserrat', sans-serif;

}

.moreprofile-card-location {
margin-top: 2%;
color: white;
display: flex;
justify-content: left;
font-family: 'Montserrat', sans-serif;
width: 95%;
}

.moreprofile-card-location-long {
margin-top: 2%;
color: white;
display: flex;
justify-content: left;
font-size: 150%;
}

.moreprofile-card-subtitle {
color: white;
display: flex;
font-family: 'Montserrat', sans-serif;
width: 100%;
}

.moreprofile-card-text {
  color: white;
display: flex;
justify-content: left;
margin-top: -0% !important;
font-size: 120%;
font-family: 'Montserrat', sans-serif;
width:100%;
}

.moreprofile-close-button {
  font-family: 'Montserrat', sans-serif;
  margin-top: 2.5%;
  cursor: pointer;
  font-size: 1.1rem;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding-left: 3.4rem;
  padding-right: 3.4rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: rgba(111, 111, 111, 1);
  transition: .25s;
  &:hover {
    /* background-color: rgb(37, 37, 37); */
    background-color: rgb(146, 96, 96);
    color: rgb(18, 18, 18);
  }
}

@keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}