.header {
    top: 0;
    z-index: 2000;
}

.header-container {
    /* margin: 0 auto; */
    width: 100%;
    // height: 100%;
    // position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    user-select: none;
}

.header-ghostd{
    display: flex;
    flex-direction: row;
    align-self: center;
}

.header-buttons{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header-logo {
    height: 5vh;
    cursor: default;
    background-color: transparent;
    align-self: center;
    opacity: .55;
    transition: .5s;
    &:hover{
        opacity: 1;
    }
}


.header-title{
    font-family: 'Nosifer', cursive;
    cursor: default;
    font-size: 300%;
    background-color: transparent;
    align-items: center;
    color: #fff;
    opacity: .55;
    transition: .5s;
    &:hover{
        opacity: 1;
    }
}



.reset-button:hover{
    transform: scale(1.35);
    opacity: 1;
    cursor: pointer;
}



.home-icon{
    height: 44px;
    opacity: 0.45;
    transition: 1s;
    filter: invert(1)
}

.home-icon:hover{
    opacity: .75;
    cursor: pointer;
}

.help-icon{
    height: 44px;
    opacity: 0.40;
    transition: .5s;
    filter: invert(1)
}

.help-icon:hover{
    opacity: .75;
    cursor: pointer;
}


@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
  }


.header-spacers{
    visibility: hidden;
}