*{
  font-family: 'Montserrat', sans-serif;
}

.large-cardContainer {
    width: 390vw;
    max-width: 460px;
    height: 490px;
      margin-bottom: 90px;
    /* margin-right: auto; */
  }

  
  
.large-card {
    /* background-color: rgb(78, 80, 78); */
    display: flex;
    flex-direction: row;
    background-color: black;
    width: 850px;
    height: 500px;
    border-radius: 10px;
    background-size: cover;
    background-position: center;
    /* cursor: pointer; */
    box-shadow: -80px -15px 25px rgba(0, 0, 0, 0.199);
    transition: all .15s;
  }

.large-img {
    display: flex;
    flex-direction: row;
    justify-content: left;
    height: 500px;
    width: 460px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }

.large-card:hover{
    box-shadow: 0px 0px 60px 0px rgba(60, 236, 183, 0.295);
  }

.location-icon-here {
   filter: invert(100%);
   height: 70%;
  }

.info-box {
  display: flex;
  justify-content: top, center;
  text-align: center;
  flex-direction: column;
   padding-left: 5%;
  padding-right: 5%; 
}

.card-title {
  color: white;
  display: flex;
  justify-content: left;
  font-size: 400%;
  padding-bottom: 0%;
  margin-top: 5%;
  margin-bottom: 1%;
  font-family: 'Montserrat', sans-serif;
}

.card-title-long {
  color: white;
  display: flex;
  justify-content: left;
  font-size: 240%;
  padding-bottom: 0%;
  margin-top: 5%;
  margin-bottom: 1%;
  font-family: 'Montserrat', sans-serif;

}

.card-location {
margin-top: 2%;
color: white;
display: flex;
justify-content: left;
font-family: 'Montserrat', sans-serif;
}

.card-location-long {
margin-top: 2%;
color: white;
display: flex;
justify-content: left;
font-size: 150%;
}

.card-subtitle {
color: white;
display: flex;
justify-content: center;
font-family: 'Montserrat', sans-serif;
}

.card-text {
  color: white;
display: flex;
justify-content: left;
margin-top: 10%;
font-size: 120%;
font-family: 'Montserrat', sans-serif;
}

.unmatch-button {
  margin-top: 10%;
  cursor: pointer;
  font-size: 1.1rem;
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding-left: 3.4rem;
  padding-right: 3.4rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: rgba(112, 111, 111, 1);
  transition: .25s;
}

.unmatch-button:hover {
  /* background-color: rgb(37, 37, 37); */
  background-color: rgb(238, 106, 92);
  color: rgb(53, 20, 20);
    animation: pump 2s linear infinite;
}

@keyframes pump {
  0% {transform: scale(0.95);}
  5% {transform: scale(1.05);}
  39% {transform: scale(0.90);}
  45% {transform: scale(1);}
  60% {transform: scale(0.95);}
  100% {transform: scale(0.925);}
}

.clicked-button {
  font-family: 'Montserrat', sans-serif;
  margin-top: 10%;
  cursor:default;
  font-size: 1.1rem;
  color: rgb(42, 42, 42);
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding-left: 3.4rem;
  padding-right: 3.4rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: rgb(84, 84, 84);
  transition: .25s;
}


