/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 15px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  /* background: #373a47; */
  background: #e5e5e6;
  -webkit-transition: .5s;
  transition: .5s;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #88dfcc;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #24252b;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #24252b;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: black;
}

/* Individual item */
.bm-item {
  display: inline-block;
  color: #d1d1d1;
  margin-bottom: 10px;
  font-size: larger;
  text-align: left;
  text-decoration: none;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}

.menu-holder {
  position: absolute;
  height: 98%;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}

.large-menu-item {
  margin-bottom: 20px;
  font-size: xx-large;
  text-align: center;
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  margin-left: 40px;
}

.spacing-menu-item {
  font-size: xx-large;
  text-align: center;
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  color: transparent;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

