* {
  font-family: 'Montserrat', sans-serif;
}

.large-cardContainer {
  width: 390vw;
  max-width: 460px;
  height: 490px;
  margin-bottom: 90px;
  /* margin-right: auto; */
}

.large-card {
  /* background-color: rgb(78, 80, 78); */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  background-color: black;
  width: 850px;
  height: 500px;
  border-radius: 10px;
  background-size: cover;
  background-position: center;
  /* cursor: pointer; */
  -webkit-box-shadow: -80px -15px 25px rgba(0, 0, 0, 0.199);
          box-shadow: -80px -15px 25px rgba(0, 0, 0, 0.199);
  -webkit-transition: all .15s;
  transition: all .15s;
}

.large-img {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: left;
      -ms-flex-pack: left;
          justify-content: left;
  height: 500px;
  width: 460px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.large-card:hover {
  -webkit-box-shadow: 0px 0px 60px 0px rgba(60, 236, 183, 0.295);
          box-shadow: 0px 0px 60px 0px rgba(60, 236, 183, 0.295);
}

.location-icon-here {
  -webkit-filter: invert(100%);
          filter: invert(100%);
  height: 70%;
}

