.sb-matchinfo-unmatch-button {
  margin-top: 5%;
  cursor: pointer;
  font-size: 1.1rem;
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding-left: 3.4rem;
  padding-right: 3.4rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: #706f6f;
  -webkit-transition: .25s;
  transition: .25s;
}

.sb-matchinfo-unmatch-button:hover {
  /* background-color: rgb(37, 37, 37); */
  background-color: #ee6a5c;
  color: #351414;
  -webkit-animation: pump 2s linear infinite;
          animation: pump 2s linear infinite;
}

@-webkit-keyframes pump {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  5% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  39% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  45% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    -webkit-transform: scale(0.925);
            transform: scale(0.925);
  }
}

@keyframes pump {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  5% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  39% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  45% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    -webkit-transform: scale(0.925);
            transform: scale(0.925);
  }
}

